.blog {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    height: 20vw;
    margin-bottom: 40px;
    background-color: #fff;
    text-decoration-line: none;
    
    .blogShadow {
      position: absolute;
      left: 0%;
      top: 0%;
      right: 0%;
      bottom: 0%;
      z-index: 20;
      
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      padding-left: 40px;
      -webkit-box-align: center;
      -webkit-align-items: center;
      -ms-flex-align: center;
      align-items: center;
      grid-auto-columns: 1fr;
      -ms-grid-columns: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
      -ms-grid-rows: auto auto;
      grid-template-rows: auto auto;
      background-color: rgba(0, 0, 0, 0.4);
      opacity: 0;
      -webkit-transition: opacity 500ms ease;
      transition: opacity 500ms ease;
    }
  }

  
.blogShadow:hover {
    background-color: rgba(0, 0, 0, 0.4);
    opacity: 1;
  }

  .blogShadow p{
    font-family: Montserrat, sans-serif;
    color: #fff;
  }

  .blogImgW {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    width: 28vw;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
  }

   
  .blogText {
    overflow: hidden;
    margin: 20px 40px;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;

  }


  
  .blogHeadline {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    margin: 12px 0 0 0;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-left:4px ;
  }
  
  .blogTitle {
    font-family: Montserrat, sans-serif;
    color: #000;
    font-size: 18px;
    line-height: 24px;
    font-weight: 600;
    position: relative;
    z-index: 100;
  }
  .greenUnderline{
    position: absolute;
    top: 10px;
    left: -4px;
    width: calc(100% + 8px);
    height: 8px;
    background-color:rgba(103, 255, 50) ;
    z-index: 0;
  }
  .blogTime {
    font-family: Montserrat, sans-serif;
    color: #adadad;
  }
  
  .blogIntro {
    font-family: Montserrat, sans-serif;
    color: #adadad;
    font-size: 14px;
    line-height: 2;
  }
  
  .blogImg {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
  }
  .blogTag{
    margin-bottom: 16px;
    color:  #adadad;

  }


@media screen and (max-width: 767px) {
    .blog {
        height: auto;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
    }
    .blogShadow {
        padding-top: 60px;
        padding-left: 0px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
    }
    .blogImgW {
      width: 100%;
      height: 50vw;
      -webkit-align-content: space-between;
      -ms-flex-line-pack: justify;
      align-content: space-between;
    }
    

}