#popTrack-hero-image {
    background-image: url(https://rfarn.com/media/fakenews2.png);
    background-repeat: no-repeat;
    background-position: 60% 100%;
    background-size: auto 100%;
  }
#popTrack-summary {
    background-color: rgba(255, 98, 126, 0.3);
}
.popTrack-insight-label {
    border-right: 3px solid #FF627E;
  }
.popTrack-feature h2 {
    color: #FF627E;
}
#popTrack-prototype {
    width: 100%;
    box-sizing: border-box;
    padding: 0px;
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 16px;
  }
  
  #popTrack-prototype img {
    width: 100%;
    height: auto;
  }
  
  
  
@media (min-width: 576px) and (max-width: 991px) {
    #popTrack-hero-image {
      background-size: auto 90%;
      background-position: 50% 100%;
    }
    #popTrack-hero-image .hero-image-cover {
        background: rgba(255, 255, 255, 0.55);
      }
    #popTrack-prototype {
        padding: 0px 16px;
    }
}

@media (max-width: 575px) {
    #popTrack-hero-image{
        background-size: auto 50%;
        background-position: 65% 100%;
    }
    .popTrack-insight-label {
        border-bottom: 3px solid #FF627E;
        border-right: none;
      }
      #popTrack-prototype {
        grid-template-columns: repeat(3, 1fr);
      }
      #popTrack-hero-image .hero-image-cover {
        background: rgba(255, 255, 255, 0.5);
      }
}