@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,800);
.footerpart {
    display: flex;
    height: 10vh;
    justify-content: center;
    align-items: center;

  }
  
  .footertext {
    display: flex;
    font-family: Montserrat, sans-serif;
    color: #adadad;
    text-align: center;
  }

  @media screen and (max-width: 991px) {
      
    .footertext {
        font-size: 12px;
    }


  }
  
.blog {
  position: relative;
  display: flex;
  height: 20vw;
  margin-bottom: 40px;
  background-color: #fff;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
.blog .blogShadow {
  position: absolute;
  left: 0%;
  top: 0%;
  right: 0%;
  bottom: 0%;
  z-index: 20;
  display: flex;
  padding-left: 40px;
  align-items: center;
  grid-auto-columns: 1fr;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  -ms-grid-rows: auto auto;
  grid-template-rows: auto auto;
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 0;
  transition: opacity 500ms ease;
}
.blogShadow:hover {
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 1;
}
.blogShadow p {
  font-family: Montserrat, sans-serif;
  color: #fff;
}
.blogImgW {
  display: flex;
  overflow: hidden;
  width: 28vw;
  justify-content: flex-start;
  align-items: center;
}
.blogText {
  overflow: hidden;
  margin: 20px 40px;
  flex: 1 1;
}
.blogHeadline {
  display: flex;
  margin: 12px 0 0 0;
  justify-content: space-between;
  padding-left: 4px ;
}
.blogTitle {
  font-family: Montserrat, sans-serif;
  color: #000;
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
  position: relative;
  z-index: 100;
}
.greenUnderline {
  position: absolute;
  top: 10px;
  left: -4px;
  width: calc(100% + 8px);
  height: 8px;
  background-color: rgba(103, 255, 50);
  z-index: 0;
}
.blogTime {
  font-family: Montserrat, sans-serif;
  color: #adadad;
}
.blogIntro {
  font-family: Montserrat, sans-serif;
  color: #adadad;
  font-size: 14px;
  line-height: 2;
}
.blogImg {
  flex: 1 1;
}
.blogTag {
  margin-bottom: 16px;
  color: #adadad;
}
@media screen and (max-width: 767px) {
  .blog {
    height: auto;
    flex-direction: column;
    align-items: center;
  }
  .blogShadow {
    padding-top: 60px;
    padding-left: 0px;
    flex-direction: column;
  }
  .blogImgW {
    width: 100%;
    height: 50vw;
    align-content: space-between;
  }
}


  
  .aboutIntro {
    display: flex;
    min-height: 66.25vh;
    align-items: center;
    background-color: #252525;
    color: #f1f1f1;
  }
  
  .aboutMe {
    overflow: hidden;
    min-height: 66.25vh;
    background-image: url("https://mxy-1307230413.cos.ap-shanghai.myqcloud.com/img/aboutme.jpg");
    background-position: 50% 50%;
    background-size: cover;
  }
  
  .watermark {
    position: absolute;
    left: auto;
    top: 2%;
    right: 3%;
    bottom: auto;
  }
  
  .watermarkimg {
    height: 5vw;
  }


  @media screen and (max-width: 767px) {
    .aboutMe {
        height: 30vh;
      }
      .watermark {
        left: auto;
        top: auto;
        right: 5%;
        bottom: 23%;
      }
    
      .watermarkimg {
        height: 10vw;
      }

    
  }
  @media screen and (max-width: 479px) {
    .watermarkimg {
        height: 16vw;
      }
  }
