   /* ==========================================================================
      Start of custom Webflow CSS
      ========================================================================== */ 
    
   .layoutGrid {
     display: -ms-grid;
     display: grid;
     grid-auto-columns: 1fr;
     -ms-grid-columns: 1fr 1fr;
     grid-template-columns: 1fr 1fr;
     -ms-grid-rows: auto auto;
     grid-template-rows: auto auto;
     grid-row-gap: 16px;
     grid-column-gap: 16px;
   }

   .aboutGrid {
    grid-column-gap: 24px;
    -ms-grid-columns: 1.75fr 1fr;
    grid-template-columns: 1.75fr 1fr;
    -ms-grid-rows: auto;
    grid-template-rows: auto;
  }
   
   /* body,html{
    scroll-behavior: smooth;
  }
   */


   .main_border_updown{
     width: 100%;
     height: 16px;
     position: fixed;
     left: 0;
     z-index: 999;
   }
   .homesection {
     display: flex;
     overflow: hidden;
     height: 100vh;
     flex-direction: column;
     justify-content: center;
     align-items: center;
   }
   
   .NavSection {
     position: relative;
     display: flex;
     height: 10vh;
     flex-direction: row;
     align-items: center;
     justify-content: center;
   }
   

   
   .signblock {
    position: absolute;
    top: 0%;
    bottom: auto;
    width: 200px;
    margin-top: 16px;
   }
   
   .introwrap {
     max-width: 1080px;
     padding-right: 20px;
     padding-left: 20px;
   }
   
   .paragraph {
     font-family: Montserrat, sans-serif;
     font-size: 20px;
     line-height: 40px;
     text-align: center;
   }
   
   .scrolltag {
     position: absolute;
     top: auto;
     right: auto;
     bottom: 0%;
   }
   
   .greenblock {
     display: flex;
     width: 64px;
     height: 102px;
     justify-content: center;
     align-items: center;
     background-color: rgba(103, 255, 50);    /* 主题色 */
     transition: height 400ms ease;
     text-align: center;
   }
   
   .greenblock:hover {
     height: 126px;
   }
   
   .arrow {
     display: inline-block;
     text-align: left;
   }
   
   .workSection {
     width: 85vw;
     background-color: transparent;
   }
   
   .blogsection {
     width: 85vw;
   }
   
   .gallerysection {
     width: 85vw;
   }
   
   .aboutSection {
     display: block;
     width: 96vw;
     padding-right: 5vw;
     padding-left: 5vw;
   }
   
   .signBlockNav {
     position: relative;
     top: 0%;
     bottom: auto;
     width: 200px;
     margin-top: 16px;
     margin-bottom: 8px;
   }
   
   .mainPart {
     padding-bottom: 3.75vh;
     background-color: #f1f1f1;
   }
   
   .tabs {
     display: flex;
     flex-direction: column;
     align-items: center;
   }
   
   .tabLink {
     margin-right: 20px;
     margin-left: 20px;
     background-color: transparent;
     font-family: Montserrat, sans-serif;
     color: #adadad;
     text-align: center;
   }
   
   .tabLink:hover {
     color: #6369a750;
     font-weight: 800;
   }
   
   .tabLink.w--current {
     background-color: transparent;
     font-family: Montserrat, sans-serif;
     color: #252525;
     font-weight: 800;
     text-align: center;
   }
   
   .tabs-menu {
     height: 4vh;
     margin-bottom: 6vh;
   }
   
   .tabs-content {
     display: flex;
     flex-direction: column;
     align-items: center;
   }
   
   .work {
     text-decoration: none;
   }
   
   .worktext {
     margin-top: 10px;
   }
   
   .workheadline {
     display: flex;
     justify-content: space-between;
   }
   
   .worktitle {
     font-family: Montserrat, sans-serif;
     color: #000;
     font-size: 16px;
     line-height: 24px;
     font-weight: 600;
   }
   
   .worktitle:hover {
     text-decoration: underline;
