 /*FADE IN*/
 .fade {
   opacity: 0;
   -webkit-transform: translateY(20px);
           transform: translateY(20px);
   transition: opacity 0.75s ease-in-out, -webkit-transform 0.75s ease-in-out;
   transition: opacity 0.75s ease-in-out, transform 0.75s ease-in-out;
   transition: opacity 0.75s ease-in-out, transform 0.75s ease-in-out, -webkit-transform 0.75s ease-in-out;
 }
 
 .fade.appear {
   opacity: 1;
   -webkit-transform: translateY(0px);
           transform: translateY(0px);
 }
 
 /*MODALS*/
 .modal {
   position: fixed;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   display: none;
   justify-content: center;
   align-items: center;
   background-color: rgba(0, 0, 0, 0.25);
   z-index: 15;
 }
 
 .modal img {
   width: auto;
   height: auto;
   max-width: calc(83.333% - 64px);
   max-height: 80%;
   object-fit: contain;
 }
 
 /*BACK TO TOP BUTTON*/
 .back-to-top {
   position: fixed;
   width: 30px;
   height: 30px;
   background-color: #00071c;
   bottom: 30px;
   left: 91.667%;
   border-radius: 100%;
   border:none;
   display: none;
   align-items: center;
   justify-content: center;
 }
 
 .back-to-top:hover {
   background-color: #093ACE;
 }
 
 .project-arrow {
   color: white;
   font-size: 16px;
 }
 
 @media (min-width: 576px) and (max-width: 991px) {
   .project-arrow {
     font-size: 24px;
   }
   .back-to-top {
     left: 83.333%;
     width: 45px;
     height: 45px;
   }
 }
 
